import style from "./footer.module.css";
import sns1 from "./images/insta.png"
import sns2 from "./images/twitter.png"
import sns3 from "./images/facebook.png"
import logo from "./images/8817.png"
import MediaQuery from "react-responsive/"

function Footer(){
    return(
        <>
        <MediaQuery minWidth={676} maxWidth={1000}>
            <div className={style.Tfooter}>
                <div className={style.sns}>
                    <ul>
                        <li className={style.insta}><img src={sns1}/></li>
                        <li className={style.twitter}><img src={sns2}/></li>
                        <li className={style.facebook}><img src={sns3}/></li>
                    </ul>
                    <div className={style.clear}></div>

                </div>
                <div className={style.logo}>
                    <img src={logo}/>
                    <p>日本の温泉</p>
                </div>
            </div>
        </MediaQuery>
        <MediaQuery minWidth={1000}>
            <div className={style.footer}>
                <div className={style.sns}>
                    <ul>
                        <li className={style.insta}><img src={sns1}/></li>
                        <li className={style.twitter}><img src={sns2}/></li>
                        <li className={style.facebook}><img src={sns3}/></li>
                    </ul>
                    <div className={style.clear}></div>

                </div>
                <div className={style.logo}>
                    <img src={logo}/>
                    <p>日本の温泉</p>
                </div>
            </div>
        </MediaQuery>
        <MediaQuery maxWidth={676}>
            <div className={style.Kfooter}>
                <div className={style.sns}>
                    <ul>
                        <li className={style.insta}><img src={sns1}/></li>
                        <li className={style.twitter}><img src={sns2}/></li>
                        <li className={style.facebook}><img src={sns3}/></li>
                    </ul>
                    <div className={style.clear}></div>

                </div>
                <div className={style.logo}>
                    <img src={logo}/>
                    <p>日本の温泉</p>
                </div>
            </div>
        </MediaQuery>

        </>
    )
}
export default Footer