import React from 'react';
import Swiper from 'react-id-swiper';
import "swiper/css/swiper.css";
import Image1 from "./images/img2-1.jpg"
import Image2 from "./images/img2-2.jpg"
import Image3 from "./images/img2-3.jpg"
import Image4 from "./images/img2-4.jpg"
import style from "./slideWrapper.module.css"

class SlideWrapper extends React.Component {
    constructor(props) {
      super(props)
      this.state = {
        params: {
          spaceBetween: 30,
          autoplay:{
              delay:3000,
              disableOnInteraction:false
          },
          effect:"fade"
        }
      }
    }
  
    render() {
      return(
        <Swiper {...this.state.params}>
          <div className={style.img}><img src={Image1}/></div>
          <div className={style.img}><img src={Image2}/></div>
          <div className={style.img}><img src={Image3}/></div>
          <div className={style.img}><img src={Image4}/></div>
        </Swiper>
      )
    };
  }
export default SlideWrapper