import style from "./onsenItem.module.css"
import MediaQuery from "react-responsive"
function OnsenItem(props){
    return(
        <>
        <MediaQuery minWidth={700} maxWidth={1000}>
            <div className={style.TItem}>
            <img src={props.img}/>
            <h3>{props.onsen}</h3>
            <p>{props.children}</p>
            </div>
        </MediaQuery>
        <MediaQuery minWidth={1000}>
            <div className={style.Item}>
            <img src={props.img}/>
            <h3>{props.onsen}</h3>
            <p>{props.children}</p>
            </div>
        </MediaQuery>
        <MediaQuery maxWidth={700}>
            <div className={style.KItem}>
            <img src={props.img}/>
            <h3>{props.onsen}</h3>
            <p>{props.children}</p>
            </div>
        </MediaQuery>
        </>
    )
}
export default OnsenItem