import style from "./meisen.module.css"
import OnsenItem from "./onsenIntro"
import image1 from "./images/dougo.jpg"
import image2 from "./images/arima2.jpg"
import image3 from "./images/sirahama.jpg"
import imageBack1 from "./images/151256.png"
import imageBack2 from "./images/137920.png"
import MediaQuery from "react-responsive/"

function Kosen(){
    return(
        <>
        <MediaQuery minWidth={700} maxWidth={1000}>
            <div className={style.TmeisenWrapper}>
                <h2> 日本三大古泉</h2>
                
                <OnsenItem onsen="道後温泉（愛媛県）" img={image1}>
                三千年の歴史を誇るといわれ、聖徳太子、天智天皇、天武天皇といった皇族や夏目漱石、正岡子規などの著名人も訪れた由緒ある温泉です。
                </OnsenItem>
                <OnsenItem onsen="有馬温泉（兵庫県）" img={image2}>
                兵庫県神戸市にある有馬温泉は日本書紀の中で舒明天皇が西暦631年に滞在したと言う記述があり、飛鳥時代にはすでに存在が確認されている温泉です。
                </OnsenItem>
                <OnsenItem onsen="白浜温泉（和歌山県）" img={image3}>
                千数百年の歴史持つ温泉で、657年に孝徳天皇の子どもである有間皇子が訪れたと言われています。
                </OnsenItem>
                <div className={style.clear}></div>
                <img className={style.Tnoren} src={imageBack1}/>
                <img className={style.Tkuchi} src={imageBack2}/>
            </div>
        </MediaQuery>
        <MediaQuery minWidth={1000}>
            <div className={style.meisenWrapper}>
                <h2> 日本三大古泉</h2>
                
                <OnsenItem onsen="道後温泉（愛媛県）" img={image1}>
                三千年の歴史を誇るといわれ、聖徳太子、天智天皇、天武天皇といった皇族や夏目漱石、正岡子規などの著名人も訪れた由緒ある温泉です。
                </OnsenItem>
                <OnsenItem onsen="有馬温泉（兵庫県）" img={image2}>
                兵庫県神戸市にある有馬温泉は日本書紀の中で舒明天皇が西暦631年に滞在したと言う記述があり、飛鳥時代にはすでに存在が確認されている温泉です。
                </OnsenItem>
                <OnsenItem onsen="白浜温泉（和歌山県）" img={image3}>
                千数百年の歴史持つ温泉で、657年に孝徳天皇の子どもである有間皇子が訪れたと言われています。
                </OnsenItem>
                <div className={style.clear}></div>
                <img className={style.noren} src={imageBack1}/>
                <img className={style.kuchi} src={imageBack2}/>
            </div>
        </MediaQuery>
        <MediaQuery maxWidth={700}>
            <div className={style.KmeisenWrapper}>
                <h2> 日本三大古泉</h2>
                
                <OnsenItem onsen="道後温泉（愛媛県）" img={image1}>
                三千年の歴史を誇るといわれ、聖徳太子、天智天皇、天武天皇といった皇族や夏目漱石、正岡子規などの著名人も訪れた由緒ある温泉です。
                </OnsenItem>
                <OnsenItem onsen="有馬温泉（兵庫県）" img={image2}>
                兵庫県神戸市にある有馬温泉は日本書紀の中で舒明天皇が西暦631年に滞在したと言う記述があり、飛鳥時代にはすでに存在が確認されている温泉です。
                </OnsenItem>
                <OnsenItem onsen="白浜温泉（和歌山県）" img={image3}>
                千数百年の歴史持つ温泉で、657年に孝徳天皇の子どもである有間皇子が訪れたと言われています。
                </OnsenItem>
                <div className={style.clear}></div>
            </div>
        </MediaQuery>
        
        </>
    )
}
export default Kosen