import Image from "./images/8817.png"
import style from "./header.module.css"
import MediaQuery from "react-responsive/"
import Burger from "./Nav/Burger"
export function Header(){
    return(
        <>
        <MediaQuery minWidth={700} maxWidth={1000}>
            <div className={style.header}>
                <div className={style.logo}>
                    <img src={Image}/>
                    <p>日本の温泉</p>
                </div>
                <div className={style.menu}>
                    <ul>
                        <li><a>Home</a></li>
                        <li><a>温泉地</a></li>
                        <li><a>温泉宿</a></li>
                        <li><a>お知らせ</a></li>
                        <li><a>ブログ</a></li>
                    </ul>

                </div>
                <div className="clear"></div>
            </div>
        </MediaQuery>
        <MediaQuery minWidth={1000}>
            <div className={style.header}>
                <div className={style.logo}>
                    <img src={Image}/>
                    <p>日本の温泉</p>
                </div>
                <div className={style.menu}>
                    <ul>
                        <li><a>Home</a></li>
                        <li><a>温泉地</a></li>
                        <li><a>温泉宿</a></li>
                        <li><a>お知らせ</a></li>
                        <li><a>ブログ</a></li>
                    </ul>

                </div>
                <div className="clear"></div>
            </div>
        </MediaQuery>
        <MediaQuery maxWidth={700}>
            <div className={style.Kheader}>
                <div className={style.Klogo}>
                    <img src={Image}/>
                    <p>日本の温泉</p>
                </div>
                <div className={style.menubar}>
                    <Burger />
                </div>
                
            </div>
        </MediaQuery>
        
        </>
    )
}