import './App.css';
import React,{useState,useEffect} from "react";
import Footer from './component/footer';
import { Header } from './component/header';
import Kosen from './component/kosenWrapper';
import Meisen from './component/meisenWrapper';
import SentenceWrapper from './component/sentenceWrapper';
import SlideWrapper from './component/slideWrapper';
import RingLoader from "react-spinners/RingLoader";

function App() {
  const [loading,setLoading] =useState(false)

  useEffect(()=>{
    setLoading(true)
    setTimeout(()=>{
      setLoading(false)
    },1000)
  },[])
  return (
    <div className="App">
      {loading ?
      <div className="loader">
        <RingLoader color={"black"} loading={loading} size={150} />
      </div>
      
      :
      <>
        <Header/>
        <SlideWrapper/>
        <SentenceWrapper/>
        <Meisen />
        <Kosen/>
        <Footer />
      </>
      }
      
    </div>
  );
}

export default App;
