import React from 'react';
import styled from 'styled-components';

const Ul = styled.ul`
list-style: none;
display: flex;
li {
    padding: 18px 10px;
}
flex-flow: column nowrap;
background-color: rgba(82, 64, 3, 0.8);
position: fixed;
z-index:10;
transform: ${({ open }) => open ? 'translateX(0)' : 'translateX(100%)'};
top: 0;
right: 0;
height: 100vh;
width: 300px;
padding-top: 3.5rem;
transition: transform 0.3s ease-in-out;
li {
  color: #fff;
`;

const RightNav = ({ open }) => {
  return (
    <Ul open={open}>
      <li><a>Home</a></li>
      <li><a>温泉地</a></li>
      <li><a>温泉宿</a></li>
      <li><a>お知らせ</a></li>
      <li><a>ブログ</a></li>
    </Ul>
  )
}

export default RightNav